import React,{Component} from 'react';
import Guada from './../Imagenes/Guada.png'
import './MisTrabajos.css'

class MisTrabajos extends Component{
    constructor(props){
        super(props)
        this.state={
            trabajos:[{
                Titulo:'La Guadalupana',
                Image:Guada,
                Descripcion:'Pagina web para importante fabrica de pastas de la localidad de Rio Cuarto.',
                Enlace:'http://www.laguadalupana.com.ar'
            }],
            seleccion:undefined
        }
    }
    render(){
        return(
            <div className='Trabajos'>
                <div className='misTrabajos'>
                    <h1>Mis Trabajos</h1>
                    {this.state.trabajos.map((d,i)=>{
                        return(
                            <div key={i} a={i} className="Trabajo">
                                <h3>{d.Titulo}</h3>
                                <img src={d.Image} alt=""/>
                            </div>
                        )
                    })}
                </div>
                {this.mas()}
            </div>
        )
    }
    componentDidMount(){
        document.querySelectorAll('.Trabajo').forEach((e)=>{
            e.addEventListener('click',(a)=>{
                let ref =a.currentTarget
                this.setState({
                    seleccion:ref.getAttribute('a')
                })
                this.rotar()
            })
        })
    }
    mas= ()=>{
        if(this.state.seleccion!==undefined){
            let ref=this.state.trabajos[this.state.seleccion]
            return(
                <div className='masTrabajo Rotar'>
                    <h3>{ref.Titulo}</h3>
                    <img src={ref.Image} alt=""/>
                    <p>{ref.Descripcion}</p>
                    <div className='Atras' onClick={this.atras}>
                        Atras
                    </div>
                    <a href={ref.Enlace} target='_blank' rel="laguadalupana noopener noreferrer">
                        Enlece
                    </a>
                </div>
            )
        }
    }
    atras=()=>{
            document.querySelector('.masTrabajo').classList.add('Rotar')
            document.querySelector('.misTrabajos').classList.remove('Rotar')
            window.setTimeout(()=>{this.setState({
                seleccion:undefined
            })},300)
    }
    rotar= ()=>{
        if(this.state.seleccion!==undefined){
            window.setTimeout(()=>{
                document.querySelector('.masTrabajo').classList.remove('Rotar')
                document.querySelector('.misTrabajos').classList.add('Rotar')
            },100)
        }
    }
}


export default MisTrabajos;